@font-face {
    font-family: 'NanumBarunpen';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/NanumBarunpen.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.navbar-main-div {
    width: 100%;
    height: 60px;
    background-color: var(--color-identity);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-content: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    align-content: center;
    align-items: center;
}

a.navbar-logo:active,
a.navbar-logo:link,
a.navbar-logo:visited,
a.navbar-logo:hover {
    text-decoration: none;
    color: var(--color-text);
    font-size: 30px;
    font-family: 'NanumBarunpen';
}

.navbar-container-left {
    margin-left: 10px;
}

.navbar-container-right {
    margin-right: 10px;
}

a.navbar-items {
    color: var(--color-text);
    font-family: 'NanumBarunpen';
    text-decoration: none;
    font-size: 25px;
    width: 100%;
    display: inherit;
    justify-content: center;
}

a.navbar-items:hover,
a.navbar-items:active {
    background-color: var(--color-background);
    color: var(--color-highlight);
}

a.navbar-menu-btn {
    color: var(--color-text);
    font-family: 'NanumBarunpen';
    text-decoration: none;
    padding: 5px;
    font-size: 25px;
}

a.navbar-menu-btn:hover,
a.navbar-menu-btn:active {
    /* background-color: gray; */
    color: var(--color-highlight);
}

.navbar-item-list {
    display: flex;
    align-items: center;
    flex-direction: column;
    list-style: none;
    position: fixed;
    right: 0;
    top: 44px;
    background-color: var(--color-primary);
    padding: 5px;
    border: 1px solid var(--color-identity);
}

.show {
    display: flex;
}

.hide {
    display: none;
}

@media all and (min-width: 1280px) {
    .navbar-item-list {
        min-width: 10%;
    }
}

@media all and (max-width: 1279px) {
    .navbar-item-list {
        width: 100%;
    }
}
