.profile-main-div {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    padding-top: 10px;
    padding-bottom: 30px;
}

.profile-default-text {
    color: var(--color-title);
    font-family: 'NanumSquareRound';
}

.profile-icon {
    color: var(--color-title);
    font-size: 30px;
    padding: 5px;
}

.profile-image {
    height: auto;
    border-radius: 50%;
}

.line-break-profile {
    flex-basis: 100%;
    height: 30px;
    border-bottom: 1px solid var(--color-primary);
    margin-bottom: 10px;
}

@media all and (min-width: 1280px) {
    .profile-image {
        width: 300px;
        padding: 20px;
    }
}

@media all and (max-width: 1279px) {
    .profile-image {
        width: 70%;
    }
}
