.footer-text-span {
    font-family: 'NanumBarunpen';
    font-size: 20px;
}

.footer-main-div {
    justify-content: center;
    text-align: center;
}

.footer-main-div > a {
    font-family: 'NanumBarunpen';
    font-size: 20px;
}

.footer-main-div > a:hover,
.footer-main-div > a:active {
    color: var(--color-text);
}

a.vc {
    pointer-events: none;
    cursor: default;
}
